import React from 'react';
import {NavLink} from 'react-router-dom'
let FoodShowList= (props)=>{
      let catagareyname = props.title
      return(
            <>
                  <div className="col-sm-4 col-md-3 px-1  col-6">
                        
                        <NavLink exact activeClassName='active' to={{ pathname: `/catagarey/${props.title}` }}>
            <div className='FoodListbg '>
                              <div className=' bg-imgesettings' style={{ backgroundImage: `url(${props.imge})` }}>
                        {/* <img src={Food1} className='img-fluid' alt="" /> */}
                       <div className='inerbgFoodlist'>
                              <h5 className='foodListTitle'>{props.title}</h5>
                       </div>
                  </div>
            </div>
                        </NavLink> 
      </div>
            </>
      )
}
export default FoodShowList;