import React from 'react';
import $ from "jquery";


let Header = ()=>{
      var $ = require("jquery");
      let ShowBar = () => {
            $(".sideBar").css('left', '0px')
      }
      let CloseBar = () => {
            $(".sideBar").css('left', '-180px')
      }
      return (
            <>
                  <header className='header-Comtonet  sticky-top shadow'>
                        <div className="container-fluid ">
                              <div className="row">
                                    <div className="col-3 pl-3"><span className='mr-3 d-none'><i class="fas fa-arrow-left"></i></span><span onClick={ShowBar}><i className="fas fa-bars"></i></span> </div>
                                    <div className="col-6"><h4 className='text-center'><i className="fas fa-utensils mr-1"></i>FOOD</h4></div>
                                    <div className="col-3 pr-3 text-right"><span><i class="fas fa-shopping-bag"></i><span className='CatIconList'>0</span></span></div>
                              </div>
                        </div>
                        <div className="sideBar">
                              <div className="container-fluid">
                                    <div className="row">
                                          <div className="col-12">
                                                <span onClick={CloseBar} className='float-right mt-1'><i class="fas fa-times"></i></span>
                                          </div>
                                    </div>
                                    <div className="row mt-4">
                                          <div className="col-12">
                                                <ul className='SideNavList'>
                                                      <li>Cart Details<span className='pr-1 float-right'><span><i class="fas fa-shopping-bag"></i><span className='CatIconList countSidebar'>0</span></span></span></li>
                                                      <li>Search<span className='pr-1 float-right'><i class="fas fa-search"></i></span></li>
                                                      <li>Report Error<span className='pr-1 float-right'><i class="fas fa-bug"></i></span></li>
                                                      <li>Chat<span className='pr-1 float-right'><i class="fas fa-sms"></i></span></li>
                                                      
                                                </ul>
                                          </div>
                                    </div>
                              </div>
                             
                             
                        </div>
           </header>
                 
            </>
      )
}
export default Header;