import React from 'react';
import {NavLink, useParams} from 'react-router-dom'
import imgbg from '../public/foodList/product1.jpg';
 import $ from "jquery";
 import CatagariList from './Api/CatagariPage/CatagariTem';
import CatagareData from './Api/CatagariPage/CatagareData';

let FoodCatagarey=()=>{
      let {Listid} = useParams()

      window.scrollTo(0, 0)
      var $ = require("jquery");
      $(".sideBar").css('left', '-180px')
      return(
            <>
            <div className="container-fluid">
                        <div className="row ">
                              <div className="col-12 px-2">
                                    <h5 className='mt-2 mb-1 pb-1  border-bottom'>{Listid}</h5>
                              </div>
                        </div>
                        <div className="row">
                        {
                              CatagareData.map((result)=>{
                                    return <CatagariList key={result.id}
                                    title={result.title} price={result.price} />
                              })    
                        }
                        </div>
            </div>
            </>
      )
}
export default FoodCatagarey;