import React, { useState } from 'react';
import imgbg from '../public/foodList/product1.jpg'
import $ from "jquery";
let ProductDetails = ()=>{
     const [itemCount, updateItemCount] = useState(1)
      window.scrollTo(0, 0);
      var $ = require("jquery");
      $(".sideBar").css('left', '-180px');

      let UpdateCount = () => {
            updateItemCount(itemCount + 1)
      }
      let DecCount = () => {
            if (itemCount-1>0){
                  updateItemCount(itemCount - 1)
            }
           
      }
      return (
            <>
                  <div className="container-fluid">
                 <div className="row">
                              <div className="col-12 px-0">
                                    <div className='FoodListbg2 '>
                                          <div className='bg-imgesettings' style={{ backgroundImage: `url(${imgbg})` }}>

                                          </div>
                                    </div>
                              </div>
                 </div>
                 <div className="row">
                       <div className="col-12 px-0">
                            <div className='ProductDetailsTitle px-2'>
                                
                                          <h6 className='text-right mt-n4'>Rs.5620 <small><del className='discountPersnet'>20%</del></small></h6>
                               <h5>Chease Bargar</h5>
                                          <p>Your Product Details gose here and your other detailsls gose here a
                                                ls gose here and your other detailsls gose here and your other detailsnd your other detail
                                                sls gose here and your other details also gose here </p>
                               <ul>
                                                <li>Salad with it</li>
                                                <li>Drink also With it</li>
                                                <li>2 Bonas bargar will with id</li>
                                                <li>2 Bonas bargar will with id</li>
                                                <li>2 Bonas bargar will with id</li>
                                                <li>2 Bonas bargar will with id</li>
                                               
                               </ul>
                              
                            </div>
                                    <div className="addToListContainer fixed-bottom  px-2 pt-2">
                                         <div className="row">
                                                <div className="col-6">
                                                      <div className='d-flex pt-1 justify-content-around' title='Your Cost'>
                                                            <div><span onClick={DecCount}><i class="fas fa-minus"></i></span></div>
                                                            <div className='bg-white rounded px-4 py-1 mt-n1'><span>{itemCount}</span></div>
                                                            <div><span onClick={UpdateCount}><i class="fas fa-plus"></i></span></div>
                                                      </div>
                                                </div>
                                                <div className="col-6 pr-4">
                                                      <button className='btn btn-block btn-dark '>Add To Cart <i class="fas ml-1 fa-shopping-bag"></i></button>
                                                </div>
                                         </div>
                               </div>
                       </div>
                 </div>
           </div>
            </>
      )
}
export default ProductDetails;