import React from 'react';

import { NavLink } from 'react-router-dom'
import imgbg from '../../../public/foodList/product1.jpg';
let CatagarTemp = (props)=>{
      return(
            <>
            <div className='col-md-6 col-12'>
                  <div className="row  bg-white ">
                        <div className="col-4 pl-1 pr-0">
                              <div className='FoodListbg mt-0 border-0 rounded-0'>
                                    <div className='bg-imgesettings' style={{ backgroundImage: `url(${imgbg})` }}>

                                    </div>
                              </div>
                        </div>
                        <div className="col-8 pl-1 pr-0  py-1 border-top border-bottom">
                              <div className=''>
                                    <h6>{props.title}</h6>
                                    <div className='catagaryDesContainer'>
                                          <p className='catagaryDescription'>Your descption for your poroduct anails are here.</p>
                                    </div>
                                    <div className='d-flex ratingStarts'>
                                          <span><i class="fas fa-star"></i></span>
                                          <span><i class="fas fa-star"></i></span>
                                          <span><i class="fas fa-star"></i></span>
                                          <span><i class="fas fa-star-half-alt"></i></span>
                                          <span className='text-dark'>(4.5)</span>
                                          <span className='text-dark ml-1'>Rs.480</span>
                                    </div>
                                    <NavLink exact activeClassName='active' to='/Porduct-d'><button className='FoodBanerBtn px-2 '>View</button></NavLink>
                              </div>
                        </div>
                  </div>
                  </div>
            </>
      )
}
export default CatagarTemp;