import React from 'react';
import baners1 from '../public/baners/barner1.jpg';
import foodData from './Api/Foodlist';
import FoodShowList from './Api/FoodListCom';
import $ from "jquery";
let FoodList =()=>{
      window.scrollTo(0, 0);
      var $ = require("jquery");
      $(".sideBar").css('left', '-180px')
return(
      <>
     <div className="container-fluid ">
                  <div className="row ">
                        <div className="col-12 px-2">
                              <h5 className='mt-2 mb-1 pb-1  border-bottom'>Discount Deals.</h5>
                        </div>
                  </div>
           <div className="row">
                 <div className="col-12 px-1">
                              <div className='banerbg '>
                                    <div className=' bg-imgesettings' style={{ backgroundImage: `url(${baners1})` }}>
                                          {/* <img src={Food1} className='img-fluid' alt="" /> */}
                                          <div className='inerbgFoodlist'>
                                                <button className='FoodBanerBtn px-2 '>View</button>
                                          </div>
                                    </div>
                              </div>
                 </div>
           </div>
           <div className="row ">
                 <div className="col-12 px-2">
                       <h5 className='mt-2 mb-1 pb-1  border-bottom'>All Catagares.</h5>
                 </div>
           </div>
           <div className="row">
                 {foodData.map((reuslt)=>{
                       return <FoodShowList key={reuslt.id} imge={reuslt.foodImg} title={reuslt.title}/>
                 })}
           </div>
     </div>
      </>
)
}
export default FoodList;