import React, { Component } from 'react'
import QrReader from 'react-qr-scanner'
let openturl = 0
class Scan extends Component {
      
      constructor(props) {
            super(props)
            this.state = {
                  delay: 100,
                  result: 'No result',
            }

            this.handleScan = this.handleScan.bind(this)
      }
      handleScan(data) {
            if (data!=null){
                  console.log("your data", data.text)
                    this.setState({
                  result: data.text,
            })
                  if (openturl==0){
                        openturl++
                        window.open(data.text);
                  }
            }
           
          
      }
      handleError(err) {
            console.error(err)
      }
      render() {
            const previewStyle = {
                  height: 240,
                  width: 320,
            }

            return (
                  <div>
                        Scan for your oder
                        <QrReader
                              delay={this.state.delay}
                              style={previewStyle}
                              onError={this.handleError}
                              onScan={this.handleScan}
                        />
                        <p>{this.state.result}</p>
                  </div>
            )
      }
}
export default Scan